.row {
  .btn-nav {
    width: 100%;
    max-width: 170px;

    img {
      max-height: 18px;
      max-width: 30px;
    }

    span {
      line-height: 18px;
      vertical-align: middle;
      margin-left: 4px;
      @include secondary-text;
      color: #FFF;
      font-size: 14px;
    }
  }

  .btn-wide {
    flex-basis: 100%;
  }

  .btn-narrow {
    width: auto;
    min-width: 36px;
  }

  .lesson-complete {
    @include secondary-text;
    color: #000;
    text-align: center;
    margin-top: 80px;
  }

  .btn-hint {
    width: 60px;
  }

  .btn-dark.active {
    background-color: $buttonColor;
    border-color: $buttonColor;
  }

  .btn-info {
    font-family: $secondaryFontFamily;
    font-weight: bold;
    font-size: 16px;
    background-color: $buttonColor !important;
    border: none;
    border-radius: 26.5px;
    padding: 4px 12px 6px;
    outline: none;
    vertical-align: middle;

    &:hover, &:active, &:focus {
      box-shadow: none;
      background-color: $buttonColor;
    }

    &[disabled] {
      opacity: 0.3;
      background-color: $buttonColor !important;
    }
  }

  .btn-card-button {
    border-radius: 12px;
    padding: 12px 20px;
    font-size: 18px;
    font-weight: normal;
  }

  .btn-show-lessons {
    border-radius: 15px;
    width: 100%;
    font-size: 18px;
    padding: 8px !important;
    display: inline-flex;
    justify-content: center;
    align-items: center;

    &.show {
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
    }
  }

  .btn-lesson {
    width: 100%;
    border: 1px solid $controlColor2;
    background-color: $controlColor3;
    color: $controlColor;
    padding: 10px 12px 10px 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    text-transform: uppercase;
    cursor: pointer !important;

    img {
      max-height: 22px;
    }

    &:hover {
      text-decoration: none;
      color: $controlColor;
    }

    &.activated {
      background-color: $buttonColor !important;
      color: $controlColor3;
      padding-right: 10px !important;;
    }

    &.added {
      background-color: $buttonColor !important;
      color: $controlColor3;
      padding-right: 11px !important;
    }

    &.active {
      border: 1px solid lighten($buttonColor, 10%) !important;
    }

    &.learned {
      padding-right: 9px !important;
    }
  }
}
