@media (min-width: 2501px) {
  #dict-app {
    .word-card .word-card-phrase {
      font-size: 3em;
    }

    .fullscreen {
      .word-card .word-card-phrase {
        font-size: 7em;
      }
    }
  }
}

@media (max-width: 2500px) {
  #dict-app {
    .word-card .word-card-phrase {
      font-size: 2.8em;
    }

    .fullscreen {
      .word-card .word-card-phrase {
        font-size: 5.5em;
      }
    }
  }
}

@media (max-width: 2200px) {
  #dict-app {
    .word-card .word-card-phrase {
      font-size: 2.6em;

      .fullscreen {
        .word-card .word-card-phrase {
          font-size: 4.2em;
        }
      }
    }
  }
}

@media (max-width: 1800px) {
  #dict-app {
    .word-card .word-card-phrase {
      font-size: 2.4em;

      .fullscreen {
        .word-card .word-card-phrase {
          font-size: 2.6em;
        }
      }
    }
  }
}

@media (max-width: 1200px) {
  #dict-app {
    .word-card .word-card-phrase {
      font-size: 2.2em;
    }

    .fullscreen {
      .word-card .word-card-phrase {
        font-size: 2.2em;
      }
    }
  }
}

// LG SIZE
@media (max-width: 991.98px) {
  #dict-app {
    .btn-nav {
      min-width: 140px;
      padding-right: 0;
      padding-left: 0;
    }
  }
}

// MD SIZE
@media (max-width: 767.98px) {
  #dict-app {
    .word-card .word-card-phrase {
      font-size: 2.2em;
    }

    .fullscreen {
      .word-card .word-card-phrase {
        font-size: 2.2em;
      }
    }
  }
}

// SM SIZE
@media (max-width: 575.98px) {
  #dict-app {
    .btn-nav {
      min-width: 80px;
      padding-right: 0;
      padding-left: 0;
    }

    .word-card .word-card-phrase {
      font-size: 2.0em;
    }

    .fullscreen {
      .word-card .word-card-phrase {
        font-size: 2.0em;
      }
    }
  }
}
