
#lessons-list {
  width: 230px;

  .lessons-dropdown {
    position: absolute;
    width: 230px;
    z-index: 100;
    background-color: #FFF;
    padding: 10px;
    border-radius: 0 0 10px 10px;
    border: 1px solid $controlColor;
    border-top: none;

    & > div {
      overflow-y: auto;
      height: 90vh;
      max-height: 565px;
      padding-right: 10px;
    }
  }
}