$buttonColor: #1972bc;
$controlColor: #808080;
$controlColor2: #e0e2e1;
$controlColor3: #f2f2f2;
$progressBarColor: #f2f2f2;
$cardBackgroundColor: #e3f4fc;
$cardTextColor: #4d4d4d;
$shadowColor: adjust_color(#013549, $alpha: -0.8);
$sexMale: #1D5377;
$sexFemale: #A81D45;
$mainFontFamily: 'Proxima Nova';
$secondaryFontFamily: 'Corbel';

@font-face {
  font-family: "Proxima Nova";
  font-weight: bold;
  src: url("assets/fonts/ProximaNova-Bold.eot");
  src: url("assets/fonts/ProximaNova-Bold.woff") format("woff"),
  url("assets/fonts/ProximaNova-Bold.ttf") format("truetype"),
  url("assets/fonts/ProximaNova-Bold.svg#filename") format("svg");
}

@font-face {
  font-family: "Corbel";
  font-weight: bold;
  src: url("assets/fonts/Corbel_Bold.ttf");
}

@mixin secondary-text {
  font-family: $secondaryFontFamily;
  font-weight: bold;
  color: $controlColor;
  text-transform: uppercase;
}

@keyframes fadeout {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

@keyframes fadeout-pause {
  from {
    opacity: 0.5;
  }
  to {
    opacity: 0;
  }
}

#dict-app {
  @import "~bootstrap";
  @import '~bootstrap-vue/dist/bootstrap-vue.css';
  @import 'https://cdn.lineicons.com/1.0.1/LineIcons.min.css';
  @import './lessons';
  @import './wordcard';
  @import './buttons';

  .next-step-block {
    height: 66px;
    padding-right: 30px;

    .learnt {
      img {
        display: none !important;
      }

      &.filled {
        background-color: inherit !important;
        cursor: inherit;
        border-radius: 100%;
        border: none;

        .radial-progress-inner > div {
          background-color: adjust_color($buttonColor, $alpha: -0.9) !important;
        }
      }
    }

    span {
      @include secondary-text;
      font-size: 12px;
      padding-bottom: 1px;
      opacity: 0.5;

      &.active {
        color: $cardTextColor;
        opacity: 1;
      }
    }

    a {
      cursor: default;
      margin: 0 25px;
      transition: background-color 0s, border 0s;
    }

    img {
      opacity: 0.1;
    }

    p {
      opacity: 0.1;

      &.active {
        opacity: 1;
      }
    }

    a.filled {
      width: 49px;
      height: 49px;
      background-color: $buttonColor;
      border-radius: 100%;
      border: 3px solid $buttonColor;
      cursor: pointer;

      img {
        opacity: 1;
      }

      .radial-progress-container {
        .radial-progress-inner {
          position: absolute;
          top: 0;
          right: 0;
          bottom: 0;
          left: 0;
          border-radius: 50%;
          margin: 0 auto;
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
        }

        .radial-progress-inner > div {
          background-color: $buttonColor;

          img:first-of-type {
            display: none;
          }

          img:last-of-type {
            display: block;
          }
        }
      }
    }

    .radial-progress-container {
      div > img {
        width: 16px;
      }

      .radial-progress-bar {
        & > circle:first-of-type {
          display: none;
        }

        & > circle:nth-of-type(2) {
          stroke-width: 3px !important;
        }
      }

      .radial-progress-inner > div {
        background-color: adjust_color($buttonColor, $alpha: -0.9);
        display: flex;
        align-items: center;
        justify-content: center;
        width: 49px;
        height: 49px;
        border-radius: 100%;

        img:first-of-type {
          display: block;
        }

        img:last-of-type {
          display: none;
        }
      }
    }

    img.rotated {
      width: 16px;
      transform: rotate(180deg);
    }
  }

  #learn-progress-bar {
    background-color: $progressBarColor;
    margin-top: 10px;
    margin-bottom: 25px;

    .progress-bar {
      background-color: $controlColor;
      border-radius: 5px;
    }
  }

  .page-loading {
    > span {
      width: 50px;
      height: 50px;
    }
  }

  .mode-icon {
    cursor: pointer;
    width: 100px;

    p {
      @include secondary-text;
      font-size: 12px;
      margin: 3px 0 1px;
    }

    img {
      width: 18px;
    }

    &.active span {
      display: block;
      width: 20px;
      margin: auto;
      border-bottom: 3px solid $buttonColor;
    }
  }

  .fullscreen {
    overflow-x: hidden;
    background-color: #FFF !important;
    display: flex;
    padding: 20px 15px;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    & > div {
      width: 100%;
    }


    & > div:first-of-type {
      height: 100%;

      .word-card {
        margin-bottom: 30px;

        .word-card-phrase {
          //font-size: 5.5em;
        }
      }

      .play-button {
        img {
          //width: 8.5em;
        }
      }

      .pause-button {
        img {
          //width: 17em;
        }
      }
    }
  }

  .repeat-words-counter {
    position: absolute;
    padding: 3px 4px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 15px;
    background: $buttonColor;
    color: $white;
    font-size: 11px;
    min-width: 19px;
    margin-left: 53px;
    margin-top: -24px;
  }

  // disable bootstrap outline
  textarea:focus,
  textarea.form-control:focus,
  input.form-control:focus,
  input[type=text]:focus,
  input[type=password]:focus,
  input[type=email]:focus,
  input[type=number]:focus,
  [type=text].form-control:focus,
  [type=password].form-control:focus,
  [type=email].form-control:focus,
  [type=tel].form-control:focus,
  .btn:focus,
  .btn:active,
  button:focus,
  [contenteditable].form-control:focus {
    box-shadow: none !important;
  }

  .fullwidth {
    width: 100%;
  }
}
