.word-card {
  font-family: 'Proxima Nova';
  font-weight: bold;
  padding: 20px 15px;
  min-height: 400px;
  height: calc(100% - 40px);
  width: 100%;
  margin: 10px;
  background-color: $cardBackgroundColor;
  color: $cardTextColor;
  box-shadow: 3px 8px 8px 0 $shadowColor;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 45px;
  position: relative;
  right: 10px;

  &.sex-male {
    .word-card-phrase {
      color: $sexMale
    }
  }

  &.sex-female {
    .word-card-phrase {
      color: $sexFemale
    }
  }

  &:hover {
    .run-fadeout-animation {
      opacity: 1;
      animation: fadeout 1s;
      animation-delay: 3s;
      animation-fill-mode: forwards;
    }
  }

  h2 {
    width: auto;
  }

  .invis {
    opacity: 0;
    transition: opacity 0.15s;
  }

  .visible {
    opacity: 1 !important;
  }

  .dummy-row {
    height: 15%;
    visibility: hidden;
  }

  .dummy-row2 {
    height: 5%;
    visibility: hidden;
  }

  .info-row {
    display: flex;
    width: 100%;
    height: 20%;
    justify-content: space-between;
    align-items: flex-end;
    padding-bottom: 2%;

    a {
      display: inline-block;
      margin-left: 0.5vw;

      img {
        //width: 3vh;
        //max-height: 3vh;
        //min-height: 20px;invis"
        //max-height: 20px;
        //min-width: 20px;
        //height: auto;
        width: 20px;
        height: 20px;
      }
    }

    .timer-block {

      a {
        margin-left: 0;
        margin-right: 1.1vw;
      }

      .timer-dot {
        width: 4px;
        height: 4px;
        background-color: $controlColor;
        display: inline-block;
        border-radius: 2px;
        transition: 0.4s;

        &.passed {
          background-color: $buttonColor;
          box-shadow: 0 0 1px 2px adjust_color($shadowColor, $alpha: -0.1);
          &.green {
            background-color: $success;
          }
          &.yellow {
            background-color: $warning;
          }
          &.red {
            background-color: $danger;
          }
        }
      }
    }

    .settings-block {
      .play-sound-off {
        position: relative;
        right: 0.2vw;
        //width: 3vh;
        //max-height: 2.55vh;
        //min-height: 17px;
        //min-width: 20px;
        height: 17px;
        width: 20px;
        //height: auto;
      }
    }
  }

  .word-card-phrase {
    display: flex;
    font-size: 2.4em;
    height: 30%;
    max-width: 95%;
    justify-content: center;

    &:nth-of-type(1) {
      align-items: flex-start;
    }

    &:nth-of-type(2) {
      align-items: flex-end;
    }
  }

  .play-button {
    position: absolute;
    opacity: 0.5;
    cursor: pointer;
    transition: 1s visibility;
    margin-top: -15px;

    img {
      width: 4em;
    }
  }

  .pause-button {
    position: absolute;
    margin-left: 0px;
    opacity: 0.0;
    margin-top: -15px;
    cursor: pointer;
    transition: 0.3s opacity;

    img {
      width: 8em;
    }

    &.run-fadeout-animation {
      opacity: 0.5;
      animation: fadeout-pause 0.5s;
      animation-fill-mode: forwards;
      animation-delay: 2s;
    }
  }

  .show-hint-button {
    position: absolute;
    bottom: 70px;
    cursor: pointer;

    img {
      width: 4em;
    }
  }

  .word-character {
    min-width: 28px;
    border-bottom: 3px $cardTextColor solid;
    text-align: center;
    display: inline-block;
    margin: 0 5px;
    line-height: normal;

    &.shown {
      border-bottom: none;
    }
  }
}
